@import '@dpe/base/src/global';

// Navigation
//
// Package: `@dpe/navigation`
//
// Style guide: 6.navigation

// Top links
//
// Markup: nav-top.twig
//
// Style guide: 6.navigation.top

.nav-top {
  @include media-breakpoint-up(lg) {
    padding-bottom: $spacer-s;
  }
  padding-top: $spacer-s;
  padding-bottom: $spacer-m;
  background-color: $background-alt;

  .page-container {
    @include media-breakpoint-up(lg) {
      flex-direction: row;
    }
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
  }

  ul {
    font-size: $text-xs;
    margin-bottom: 0;

    &:first-child {
      @include media-breakpoint-up(lg) {
        flex-direction: row;
        margin-bottom: 0;
      }
      align-items: center;
      flex-direction: column;
      margin-bottom: $spacer-m;

      li {
        @include media-breakpoint-up(lg) {
          margin-right: $spacer-m;
        }
        margin-right: 0;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  a {
    @include media-breakpoint-up(lg) {
      padding-top: 0;
      padding-bottom: 0;
    }
    padding-top: $spacer-xs;
    padding-bottom: $spacer-xs;
    display: inline-block;

    &:hover {
      text-decoration: underline;
    }
  }
}

// Main nav
//
// A custom dropdown navigation system with support for up to 3 levels. Shown as a vertical
// menu on small screens and horizontal on larger ones.
//
// ### DPE javascript:
//
// Requires: `/js/navigation.js`\
// Dependency: `/js/polyfill.js`\
// Usage:
// ```
// document.querySelectorAll('.nav-main').forEach((obj) => {
//    const mainNavigation = new Navigation(obj, 'nav-main');
//    mainNavigation.init();
// });
// ```
//
// Markup: nav-main.twig
//
// Style guide: 6.navigation.main

$sub-navigation-width: 244px;
$sub-navigation-offset: 40px;
$sub-navigation-shadow: 0 0 $spacer-s $translucent-shadow;
$animation-distance: -.25rem;

.nav-main {
  &-wrapper {
    @include media-breakpoint-up(lg) {
      background-color: $background-rev;
      color: $foreground-rev;
    }
  }

  ul {
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      max-width: none;
      margin: 0;
      position: relative;
    }

    &.nav-main-level-1 {
      @include media-breakpoint-up(lg) {
        margin-left: -$spacer-s;
      }
    }
  }

  a {
    @include media-breakpoint-up(lg) {
      padding: $spacer-sm;
      margin: $spacer-xs;
      border-radius: $border-radius;
      border: 0;
    }
    position: relative;
    text-decoration: none;
    color: inherit;
    display: block;
    padding: $spacer-m;
    border-bottom: $border-style;

    &:hover {
      color: inherit;
      text-decoration: underline;
    }

    &.is-active {
      background-color: $background-alt;
    }
  }

  &-has-subnav {
    > a {
      padding-right: $spacer-l;

      &::after {
        @include media-breakpoint-up(lg) {
          top: calc(50% - #{$spacer-s});
          right: $spacer-s;
        }
        @include icon('chevron-down');
        content: '';
        display: inline-block;
        position: absolute;
        top: $spacer-m + $spacer-xs;
        right: $spacer-m;
        width: $spacer-m;
        height: $spacer-m;
      }
    }

    &.nav-main-level--open {
      > a {
        &::after {
          transform: rotate(180deg);
        }
      }
    }
  }

  &-level-1 {
    @include media-breakpoint-up(lg) {
      display: flex;
    }
    font-size: $text-xs;

    > .nav-main-has-subnav {
      > a {
        &::after {
          @include media-breakpoint-up(lg) {
            @include icon('chevron-down-white');
          }
        }
      }

      &.nav-main-level--open > a {
        &::after {
          // Top level arrow doesn't rotate on desktop.
          @include media-breakpoint-up(lg) {
            transform: rotate(0deg);
          }
        }
      }
    }

    > li {
      > a {
        margin: 0;
        font-weight: $font-weight-bold;

        &.is-active {
          background-color: transparent;
        }
      }

      &:last-child > a {
        border-bottom: 0;
      }
    }
  }

  &-level-2,
  &-level-3 {
    @include media-breakpoint-up(lg) {
      border-radius: $border-radius;
      box-shadow: $sub-navigation-shadow;
      width: $sub-navigation-width;
      position: absolute;
      height: auto;
    }
    font-size: $text-xs;
    display: block;
    visibility: hidden;
    position: relative;
    z-index: 100;
    opacity: 0;
    height: 0;
    left: 0;
    width: 100%;
    transform: translateX($animation-distance);
    transition: .25s opacity ease, .25s transform ease, .25s visibility ease;
    background-color: $background;
    color: $foreground;

    &.nav-main--open {
      transform: translateY(0);
      opacity: 1;
      height: auto;
      visibility: visible;
    }

    > li > a {
      @include media-breakpoint-up(lg) {
        color: inherit;
      }
      color: $text-muted;

      &:hover,
      &.nav-main-item--active {
        @include media-breakpoint-up(lg) {
          background: $background-alt;
        }
      }
    }
  }

  &-level-2 {
    @include media-breakpoint-up(lg) {
      top: $sub-navigation-offset;
    }

    .nav-main-level-2-item {
      &.nav-main-has-subnav {
        > a {
          &::after {
            @include media-breakpoint-up(lg) {
              @include icon('chevron-right');
            }
          }
        }
      }
    }

    > li > a {
      @include media-breakpoint-up(lg) {
        padding-left: $spacer-sm;
      }
      padding-left: $spacer-ml;
    }

    &--reverse {
      @include media-breakpoint-up(lg) {
        right: 0;
        left: auto;
      }
    }
  }

  &-level-3 {
    @include media-breakpoint-up(lg) {
      left: 100%;
      top: 0;
    }
    z-index: 4;

    > li > a {
      @include media-breakpoint-up(lg) {
        padding-left: $spacer-sm;
      }
      padding-left: $spacer-l;
    }

    &--reverse {
      @include media-breakpoint-up(lg) {
        left: auto;
        right: 100%;
      }
    }
  }

  &-sub-toggle {
    display: none;
  }
}

// Sidebar nav
//
// Multi level collapsile vertical navigation.
//
// ### DPE javascript:
//
// Requires: `/js/navigation.js`\
// Dependency: `/js/polyfill.js`\
// Usage:
// ```
// document.querySelectorAll('.nav-sidebar').forEach((obj) => {
//    const sidebarNavigation = new Navigation(obj, 'nav-sidebar');
//    sidebarNavigation.init();
// });
// ```
//
// Markup: nav-sidebar.twig
//
// Style guide: 6.navigation.sidebar

.nav-sidebar {
  @include media-breakpoint-up(lg) {
    max-width: 270px;
  }

  &-title {
    font-size: $text-s;
    margin-bottom: $spacer-m;
  }

  ul {
    font-size: $text-xs;
    width: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  li {
    position: relative;
    border-bottom: $border-style;
    max-width: none;
  }

  a {
    display: block;
    padding: $spacer-s 0;
    color: $foreground;
    position: relative;

    &.is-active {
      font-weight: $font-weight-bold;
    }

    &:hover {
      text-decoration: underline;
    }
  }

  &-has-subnav > a {
    padding-right: $spacer-l;
  }

  &-level--open > button {
    transform: rotate(180deg);
  }

  &-level-2,
  &-level-3 {
    display: block;
    visibility: hidden;
    height: 0;

    a {
      padding-left: $spacer-m;
    }

    li:first-of-type {
      border-top: $border-style;
    }

    li:last-of-type {
      border-bottom: 0;
    }

    &.nav-sidebar--open {
      height: auto;
      visibility: visible;
    }
  }

  &-level-3 a {
    padding-left: $spacer-l;
  }

  &-sub-toggle {
    position: absolute;
    top: 3px;
    right: 0;
    border: 0;
    padding: 0;
    margin: 0;
    height: $spacer-l;
    width: $spacer-l;
  }
}

// Footer navs.
.nav-footer-menu {
  &,
  ul {
    font-size: $text-xs;
  }

  > li > a {
    font-weight: $font-weight-bold;
    display: inline-block;
    margin-bottom: $spacer-s;
  }
}

.nav-footer-bottom {
  @include media-breakpoint-up(lg) {
    display: flex;
    justify-content: space-between;
  }
  border-top: 1px solid $black;
  padding-top: $spacer-m;
  margin-top: $spacer-xl;

  ul {
    @include media-breakpoint-up(lg) {
      flex-direction: row;
    }
    align-items: center;
    flex-direction: column;
    font-size: $text-xxs;
    margin-bottom: 0;
  }
}

// Inline nav
//
// Markup: nav-inline.twig
//
// Style guide: 6.navigation.inline

.nav-inline {
  @include media-breakpoint-up(lg) {
    flex-direction: row;
  }
  align-items: center;
  flex-direction: column;
  display: flex;
  border-top: $border-style;
  border-bottom: $border-style;

  ul {
    @include media-breakpoint-up(lg) {
      border-bottom: 0;
      margin-right: auto;
      width: auto;
    }
    font-size: $text-xs;
    justify-content: center;
    margin-bottom: -1px;
    width: 100%;
  }

  a {
    display: inline-block;
    padding: $spacer-m 0;

    &:hover {
      text-decoration: underline;
    }
  }

  .active {
    font-weight: $font-weight-bold;
    color: $foreground;
    border-bottom: 2px solid;
    cursor: default;

    &:hover {
      text-decoration: none;
    }
  }

  .form-group {
    @include media-breakpoint-up(lg) {
      padding: 0;
      border-top: 0;
      width: auto;
    }
    padding: $spacer-s 0;
    margin-bottom: 0;
    border-top: $border-style;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  select {
    min-width: 200px;
  }

  label {
    font-size: $text-xs;
  }
}
